import React from "react";
import { Button, Grid } from "@mui/material";

function FileHeader({ file, onDelete, buttonStyle, disableRemoveButton }) {
  return (
    <Grid
      container
      justifyContent="space-between"
      alignItems="center"
      // sx={{ paddingTop: "10px" }}
    >
      <Grid item>{file.name}</Grid>

      <Grid item>
        <Button
          sx={buttonStyle}
          size="small"
          variant="outlined"
          disabled={disableRemoveButton}
          onClick={() => onDelete(file)}
        >
          REMOVE
        </Button>
      </Grid>
    </Grid>
  );
}

export default FileHeader;
