import React, { useCallback, useEffect, useState } from "react";
import {
  Grid,
  Button,
  CircularProgress,
  Snackbar,
  FormControl,
  FormHelperText,
  Select,
  MenuItem,
  InputLabel,
  Card,
  CardContent,
  Alert,
} from "@mui/material";
import { styled } from "@mui/material/styles";
import MuiAlert from "@mui/material/Alert";
import { useDropzone } from "react-dropzone";

import axiosInstance from "../config/axiosConfig";
import UploadError from "./UploadError";
import SingleFileUpload from "./SingleFileUpload";

const Dropzone = styled("div")(({ theme }) => ({
  border: `2px dashed ${theme.palette.primary.main}`,
  borderRadius: theme.shape.borderRadius,
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  backgroundColor: theme.palette.background.default,
  height: theme.spacing(10),
  outline: "none",
}));

const LoadingIndicator = styled(CircularProgress)(({ theme }) => ({
  position: "absolute",
  top: "50%",
  left: "50%",
}));

const FormControlStyled = styled(FormControl)(({ theme }) => ({
  margin: theme.spacing(2),
  minWidth: 200,
  marginBottom: "0px",
}));

// function Alert(props) {
//   return <MuiAlert elevation={6} variant="filled" {...props} />;
// }

let currentId = 0;
function getNewId() {
  return ++currentId;
}

function MutliFileUpload() {
  const [acceptedFiles, setAcceptedFiles] = useState([]);
  const [rejectedFiles, setRejectedFiles] = useState([]);
  const [notUploadedFiles, setNotUploadedFiles] = useState([]);
  const [uploadIndicator, setUploadIndicator] = useState(false);
  const [confirmationPrompt, setConfirmationPrompt] = useState({
    open: false,
    severity: "success",
    message: "Uploaded successfully",
  });
  const [uploadLocation, setUploadLocation] = useState("");
  const [isUploadLocationSelected, setIsLocationUplaodSelected] =
    useState(false);
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    if (notUploadedFiles.length > 0)
      setConfirmationPrompt((confirmationPrompt) => ({
        ...confirmationPrompt,
        message: `${notUploadedFiles.length} files not uploaded`,
        severity: "warning",
      }));
  }, [notUploadedFiles]);

  const onDropAccepted = useCallback(
    (acceptedFiles) => {
      if (notUploadedFiles.length > 0) setNotUploadedFiles([]);
      if (uploadLocation === "") setIsLocationUplaodSelected(true);
      else setIsLocationUplaodSelected(false);

      const mappedAcceptedFiles = acceptedFiles.map((acceptedFile) => ({
        file: acceptedFile,
        errors: [],
        id: getNewId(),
      }));
      setAcceptedFiles((currAccFiles) => [
        ...currAccFiles,
        ...mappedAcceptedFiles,
      ]);
    },
    [notUploadedFiles, uploadLocation]
  );

  const onDropRejected = useCallback(
    (rejecetedFiles) => {
      if (notUploadedFiles.length > 0) setNotUploadedFiles([]);
      if (uploadLocation === "") setIsLocationUplaodSelected(true);
      else setIsLocationUplaodSelected(false);

      const mappedRejecettedFiles = rejecetedFiles.map((rejectedFile) => ({
        ...rejectedFile,
        id: getNewId(),
        errors: rejectedFile.errors || [],
      }));
      setRejectedFiles((currRejFiles) => [
        ...currRejFiles,
        ...mappedRejecettedFiles,
      ]);
    },
    [notUploadedFiles.length, uploadLocation]
  );

  const onRemoveAcceptedFile = (file) => {
    setAcceptedFiles((currAccFiles) =>
      currAccFiles.filter((f) => f.file !== file)
    );
  };

  const onRemoveRejectedFile = (file) => {
    setRejectedFiles((currRejectedFiles) =>
      currRejectedFiles.filter((f) => f.file !== file)
    );
  };

  const acceptedFileList = () => {
    return acceptedFiles.map((acceptedFile) => {
      return (
        <SingleFileUpload
          key={acceptedFile.id}
          onDelete={onRemoveAcceptedFile}
          file={acceptedFile.file}
          disableRemoveButton={false}
        />
      );
    });
  };

  const rejectedFileList = () => {
    return rejectedFiles.map((rejectedFile) => {
      return (
        <Grid item key={rejectedFile.id}>
          <UploadError
            onDelete={onRemoveRejectedFile}
            file={rejectedFile.file}
            errors={rejectedFile.errors}
            disableRemoveButton={false}
          />
        </Grid>
      );
    });
  };

  const notUploadedFileList = () => {
    return notUploadedFiles.map((notUploadedFile) => {
      return (
        <Grid item key={notUploadedFile.id}>
          <UploadError
            onDelete={() => {}}
            file={notUploadedFile.file}
            errors={notUploadedFile.errors}
            disableRemoveButton={true}
          />
        </Grid>
      );
    });
  };

  // const notUploadedFileList = () => {
  //   return notUploadedFiles.map((notUploadedFile) => {
  //     return (
  //       <Grid item key={notUploadedFile.id}>
  //         {notUploadedFile.errors && notUploadedFile.errors.length > 0 && (
  //           <UploadError
  //             onDelete={() => {}}
  //             file={notUploadedFile.file}
  //             errors={notUploadedFile.errors}
  //             disableRemoveButton={true}
  //           />
  //         )}
  //       </Grid>
  //     );
  //   });
  // };

  const fileUpload = async () => {
    setNotUploadedFiles([]);
    setUploadIndicator(true);
    const uploadPromises = acceptedFiles.map(uploadFile);

    try {
      const results = await Promise.allSettled(uploadPromises);
      const rejected = results
        .filter((result) => result.status === "rejected")
        .map((result) => result.reason);
      // handleOpen();
      if (rejected.length > 0) {
        setNotUploadedFiles(rejected);
        // setConfirmationPrompt({
        //   open: true,
        //   severity: "warning",
        //   message: `${rejected.length} files not uploaded`,
        // });
      } else {
        setConfirmationPrompt({
          open: true,
          severity: "success",
          message: "Uploaded successfully",
        });
      }
    } catch (error) {
      console.log(error);
      // const errorMessage = error?.message || "Network error";
      // setNotUploadedFiles((currNotUploadedFiles) => [
      //   ...currNotUploadedFiles,
      //   {
      //     error?.file,
      //     errors: [{ code: "UPLOAD_FAILED", message: errorMessage }],
      //   },
      // ]);
    }
    setAcceptedFiles([]);
    setRejectedFiles([]);
    setUploadIndicator(false);
    setConfirmationPrompt((prevPrompt) => ({
      ...prevPrompt,
      open: true,
    }));
  };

  const uploadFile = (file, index) => {
    return new Promise((resolve, reject) => {
      const formData = new FormData();
      formData.append("file", file.file);
      formData.append("uploadLocation", uploadLocation);

      let url = `http://localhost:3000/upload?uploadLocation=${uploadLocation}`;
      // if (index === 2)
      //   url = `http://localhost:4000/upload?uploadLocation=${uploadLocation}`;

      axiosInstance
        .post(url, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => resolve(response.data))
        .catch((error) => {
          const errorMessage = error?.message || "Upload failed";
          reject({
            ...file,
            errors: [{ code: "UPLOAD_FAILED", message: errorMessage }],
          });
        });
    });
  };

  const { getRootProps, getInputProps } = useDropzone({
    onDropAccepted,
    onDropRejected,
    accept: { "image/*": [] },
    maxSize: 300 * 1024, // 300KB
  });

  const handleUploadLocationSelection = (event) => {
    setUploadLocation(event.target.value);
    setIsLocationUplaodSelected(false);
  };

  return (
    <Card sx={{ marginLeft: "20px" }}>
      <CardContent>
        <Grid container spacing={2} direction="column">
          <Grid item></Grid>
          <Grid container spacing={2} direction="column">
            <Grid item>
              <Dropzone {...getRootProps()}>
                <input {...getInputProps()} />
                <p>Drag 'n' drop some files here, or click to select files</p>
              </Dropzone>
            </Grid>
            <Grid
              container
              spacing={2}
              alignItems="flex-end"
              style={{ marginBottom: "10px" }}
            >
              <Grid item>
                <FormControlStyled
                  variant="standard"
                  required
                  error={isUploadLocationSelected}
                >
                  <InputLabel
                    id="demo-simple-select-required-label"
                    style={{ whiteSpace: "nowrap" }}
                  >
                    Select upload location
                  </InputLabel>
                  <Select
                    variant="standard"
                    labelId="demo-simple-select-required-label"
                    id="demo-simple-select-required"
                    value={uploadLocation}
                    onChange={handleUploadLocationSelection}
                  >
                    <MenuItem value={"UPLOAD_A"}>Upload to folder A</MenuItem>
                    <MenuItem value={"UPLOAD_B"}>Upload to folder B</MenuItem>
                    <MenuItem value={"UPLOAD_C"}>Upload to folder C</MenuItem>
                  </Select>
                  {isUploadLocationSelected && (
                    <FormHelperText>
                      Please select upload location
                    </FormHelperText>
                  )}
                </FormControlStyled>
              </Grid>
              <Grid item>
                <Button
                  disabled={
                    acceptedFiles.length === 0 ||
                    rejectedFiles.length > 0 ||
                    uploadLocation === ""
                  }
                  variant="contained"
                  color="primary"
                  onClick={fileUpload}
                  style={{
                    marginBottom: isUploadLocationSelected ? "15px" : "0px",
                  }}
                >
                  Upload
                </Button>
              </Grid>
            </Grid>
          </Grid>

          {rejectedFileList()}
          {acceptedFileList()}
          {notUploadedFileList()}
          {uploadIndicator && <LoadingIndicator />}
          <Snackbar
            open={confirmationPrompt.open}
            onClose={() =>
              setConfirmationPrompt((prevPrompt) => ({
                ...prevPrompt,
                open: false,
              }))
            }
            autoHideDuration={5000}
            anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
          >
            <Alert
              onClose={() =>
                setConfirmationPrompt((prevPrompt) => ({
                  ...prevPrompt,
                  open: false,
                }))
              }
              severity={confirmationPrompt.severity}
              variant="filled"
              sx={{ width: "100%" }}
            >
              {confirmationPrompt.message}
            </Alert>
          </Snackbar>
        </Grid>
      </CardContent>
    </Card>
  );
}

export default MutliFileUpload;
