import React from "react";
import { Typography } from "@mui/material";

import ErrorLinearProgress from "./ErrorLinearProgress";
import FileHeader from "./FileHeader";

function UplaodError({ file, onDelete, errors, disableRemoveButton }) {
  return (
    <>
      <FileHeader
        file={file}
        onDelete={onDelete}
        disableRemoveButton={disableRemoveButton}
        buttonStyle={
          disableRemoveButton ? null : { borderColor: "red", color: "red" }
        }
      />
      {disableRemoveButton && (
        <ErrorLinearProgress variant="determinate" value={100} />
      )}

      {errors.map((error) => (
        <div key={error.code}>
          <Typography color="error">{error.message}</Typography>
        </div>
      ))}
    </>
  );
}

export default UplaodError;
